import { Component, OnInit, Input, ViewEncapsulation, Output, EventEmitter } from '@angular/core';
import { FormDiaplyMode, FormHeader, FormField, FormDescription, FormTitleIconPosition } from '../../model';
import { Field, TextField, InputField, DropdownField, ImageField, CalendarField, AutocompleteField, CheckboxField, FileField, RadioField, SliderField, ToggleField, FieldChange, HtmlEditorField, LabelField } from '../../../field/model';
import { Button } from '../../../button/model';
import { FormGroup } from '@angular/forms';
import { CollectionUtils, ButtonUtils, FormUtils } from '../../../utility';
import { AbilityUtils } from '../../../utility/ability.utility';
import { Ability } from '@casl/ability';

@Component({
  selector: 'cf-form-header',
  templateUrl: './form-header.component.html',
  styleUrls: ['./form-header.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FormHeaderComponent implements OnInit {
  @Input() form: FormGroup;
  @Input() formHeaderConfig: FormHeader;
  @Input() description: FormDescription;
  @Input() formDisplayMode: FormDiaplyMode;
  @Input() formIdentifier: string;
  @Input() sourceIndex: number;
  @Input() widgetArrayIndex: number;
  @Input() originalData: any;
  @Input() context: any;
  @Input() formActions: Array<Field | TextField | LabelField | InputField | DropdownField | HtmlEditorField | ImageField | CalendarField | AutocompleteField | CheckboxField | FileField | RadioField | SliderField | ToggleField | Button>;
  @Output() onFieldChange = new EventEmitter();  
  @Output() onButtonClick = new EventEmitter();

  title: string;
  subtitle: string;
  iconPosition: FormTitleIconPosition;
  fields: Array<Field | TextField | LabelField | InputField | DropdownField | HtmlEditorField | ImageField | CalendarField | AutocompleteField | CheckboxField | FileField | RadioField | SliderField | ToggleField> = new Array<Field | TextField | InputField | DropdownField | HtmlEditorField | ImageField | CalendarField | AutocompleteField | CheckboxField | FileField | RadioField | SliderField | ToggleField>();
  buttons: Array<Button> = new Array<Button>();

  constructor(private ability: Ability) { 
    AbilityUtils.setAbility(this.ability);
  }

  ngOnInit() {
    this.getFormHeader();
    this.setIconPosition();
    this.saperateFormActions(); 
  }

  getFormHeader(): void {
    let title: string = this.formHeaderConfig.title;
    this.subtitle = this.formHeaderConfig.subtitle;

    switch (this.formDisplayMode) {
      case FormDiaplyMode.SEARCH: {
        title = this.formHeaderConfig.searchModeTitle ? this.formHeaderConfig.searchModeTitle : title;
      }
      case FormDiaplyMode.ADD: {
        title = this.formHeaderConfig.addModeTitle ? this.formHeaderConfig.addModeTitle : title;
      }
      case FormDiaplyMode.EDIT: {
        title = this.formHeaderConfig.editModeTitle ? this.formHeaderConfig.editModeTitle : title;
      }
      case FormDiaplyMode.VIEW: {
        title = this.formHeaderConfig.viewModeTitle ? this.formHeaderConfig.viewModeTitle : title;
      }
      default: {
        title = this.formHeaderConfig.title;
      }
    }

    if (this.widgetArrayIndex != null)  {
      title += " - " + (this.widgetArrayIndex + 1);
    }

    this.title = title;
  }

  setIconPosition() {
    this.iconPosition = FormTitleIconPosition.BEFORE_TITLE;
    if (this.formHeaderConfig.icon && this.formHeaderConfig.icon.position)  {
      this.iconPosition = this.formHeaderConfig.icon.position;
    }
  }

  saperateFormActions() {
    if (!CollectionUtils.isEmpty(this.formActions)) {
      this.formActions.forEach(formAction => {
        if (ButtonUtils.instanceOfAnyButtonType(formAction)) {
          this.buttons.push(<Button>formAction);
        } else {
          this.fields.push(<Field | TextField | LabelField | InputField | DropdownField | HtmlEditorField | ImageField | CalendarField | AutocompleteField | CheckboxField | FileField | RadioField | SliderField | ToggleField>formAction);
        }
      })

      this.initFormGroup();
    }
  }

  initFormGroup(): void {
    if (this.fields && this.fields.length > 0) {
      let formFields: Array<FormField> = new Array<FormField>();
      this.fields.map(field => formFields.push({ 'field': field, 'addMore': false }));

      this.form = FormUtils.initFormGroup(formFields, {}, {}, FormDiaplyMode.ADD);
    }
  }

  fieldChange(fieldChange: FieldChange) {
//    console.log(fieldChange);

    this.onFieldChange.emit(fieldChange); 
  }

  buttonClick(event: any) {
    this.onButtonClick.emit(event);
  }
}
